body {
  background-color: #d0f7ec;
}

.loader-container {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.js-loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.js-loader:after {
  content: "";
  box-sizing: border-box;
  width: 40px;
  height: 40px;
  border: 3px solid #ff3d00;
  border-color: #ff3d00 rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error-message {
  color: #fff;
  text-align: center;
  background-color: #ff6b6b;
  border-radius: 5px;
  padding: 10px;
  font-size: 18px;
}

.error-message button {
  color: #fff;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-top: 10px;
  font-size: 16px;
}

.error-message button:hover {
  text-decoration: underline;
}

.cat-info {
  max-width: 100%;
  background-color: #faf1e6;
  border-radius: 10px;
  gap: 30px;
  padding: 30px;
  display: flex;
}

.cat-card {
  display: none;
}

.breed-select {
  min-width: 500px;
  background-color: #fefdfd;
  border-radius: 10px;
  margin-top: 50px;
  margin-bottom: 50px;
  padding: 5px;
  font-size: 20px;
}

.loader {
  color: red;
  margin-top: 50px;
  font-size: 20px;
}

.error {
  color: red;
  font-size: 20px;
}

.js-placeholder {
  color: #0ff;
  font-size: larger;
}

.breed-select {
  color: #daa520;
}

